import React, { Component } from 'react';
import { Carousel, Divider } from 'antd';
// import Public1 from '../../assets/images/public/1.jpg';
import Public2 from '../../assets/images/public/2.jpg';
import Public3 from '../../assets/images/public/3.jpg';
import Public4 from '../../assets/images/public/4.jpg';
import styles from './index.module.scss';

class Public extends Component {
  shouldComponentUpdate() {
    return true;
  }

  render() {
    return (
      <div className={styles.container}>
        <div className={styles.titleContainer}>
          <p className={styles.title}></p>
          <p className={styles.description}></p>
        </div>
        <Carousel dots={false}>
          <div>
            <div className={styles.flex}>
              <div className={styles.left}>
                {/* <img src={Public1} alt="1" /> */}
                <video width="100%" controls src="https://cdn.youchuedu.com/v0001.mp4" />
              </div>
              <div className={styles.right}>
                <div>
                  <p className={styles.className}>-A1-14:45 </p>
                  <p className="subtitle"></p>
                </div>
                <Divider />
                <div className={styles.classDescription}>
                  <p>A1936</p>
                  <p>7 ...</p>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div className={styles.flex}>
              <div className={styles.left}>
                <img src={Public2} alt="1" />
              </div>
              <div className={styles.right}>
                <div>
                  <p className={styles.className}>3500 </p>
                  <p className="subtitle"></p>
                </div>
                <Divider />
                <div className={styles.classDescription}>
                  <p></p>
                  <p> ...</p>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div className={styles.flex}>
              <div className={styles.left}>
                <img src={Public3} alt="1" />
              </div>
              <div className={styles.right}>
                <div>
                  <p className={styles.className}> </p>
                  <p className="subtitle"></p>
                </div>
                <Divider />
                <div className={styles.classDescription}>
                  <p></p>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div className={styles.flex}>
              <div className={styles.left}>
                <img src={Public4} alt="1" />
              </div>
              <div className={styles.right}>
                <div>
                  <p className={styles.className}> </p>
                  <p className="subtitle">Alvaro</p>
                </div>
                <Divider />
                <div className={styles.classDescription}>
                  <p></p>
                  <p></p>
                </div>
              </div>
            </div>
          </div>
        </Carousel>
      </div>
    );
  }
}

export default Public;
