import React, { Component } from 'react';
import { Card } from 'antd';
import styles from './index.module.scss';
import Class1 from '../../assets/images/class/1.jpg';
import Class2 from '../../assets/images/class/2.jpg';
import Class3 from '../../assets/images/class/3.jpg';
import Class4 from '../../assets/images/class/4.jpg';

// const { Search } = Input;

class Class extends Component {
  shouldComponentUpdate() {
    return true;
  }

  render() {
    // const { searchParam, changeSearchParams, handleSearch } = this.props;
    return (
      <>
        <div className={styles.titleContainer}>
          <p className={styles.title}></p>
          <p className={styles.description}> / 11 /  / </p>
        </div>
        <div className={styles.container}>
          <Card
            className={styles.antCard}
            bordered={false}
            hoverable
            cover={(
              <img
                alt="example"
                src={Class1}
              />
            )}
            actions={[<span></span>]}
          >
            <Card.Meta
              title=""
              description=" /  TPO ..."
            />
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            hoverable
            cover={(
              <img
                alt="example"
                src={Class2}
              />
            )}
            actions={[<span></span>]}
          >
            <Card.Meta
              title="BEC"
              description="++BEC ..."
            />
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            hoverable
            cover={(
              <img
                alt="example"
                src={Class3}
              />
            )}
            actions={[<span></span>]}
          >
            <Card.Meta
              title="GREVIP"
              description=" ..."
            />
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            hoverable
            cover={(
              <img
                alt="example"
                src={Class4}
              />
            )}
            actions={[<span></span>]}
          >
            <Card.Meta
              title=""
              description=" ..."
            />
          </Card>
        </div>
      </>
    );
  }
}

export default Class;
