import React, { Component } from 'react';
import { Card } from 'antd';
import styles from './index.module.scss';
import Quality1 from '../../assets/images/quality/1.png';
import Quality2 from '../../assets/images/quality/2.png';
import Quality3 from '../../assets/images/quality/3.png';
import Quality4 from '../../assets/images/quality/4.png';
import Quality5 from '../../assets/images/quality/5.png';
import Quality6 from '../../assets/images/quality/6.png';


const headStyle = {
  border: 'none',
};

class Quality extends Component {
  shouldComponentUpdate() {
    return true;
  }

  render() {
    return (
      <>
        <div className={styles.titleContainer}>
          <p className={styles.title}></p>
          <p className={styles.description}></p>
        </div>
        <div className={styles.container}>
          <Card
            className={styles.antCard}
            bordered={false}
            headStyle={headStyle}
            hoverable
            title={<>
              <img src={Quality1} alt="1" />
            </>}
          >
            <p></p>
            <p> / TPO ...</p>
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            headStyle={headStyle}
            hoverable
            title={<>
              <img src={Quality2} alt="1" />
            </>}
          >
            <p></p>
            <p>++BEC</p>
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            headStyle={headStyle}
            hoverable
            title={<>
              <img src={Quality3} alt="1" />
            </>}
          >
            <p></p>
            <p> / TPO ...</p>
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            headStyle={headStyle}
            hoverable
            title={<>
              <img src={Quality4} alt="1" />
            </>}
          >
            <p></p>
            <p> / TPO ...</p>
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            headStyle={headStyle}
            hoverable
            title={<>
              <img src={Quality5} alt="1" />
            </>}
          >
            <p></p>
            <p>++BEC ...</p>
          </Card>
          <Card
            className={styles.antCard}
            bordered={false}
            headStyle={headStyle}
            hoverable
            title={<>
              <img src={Quality6} alt="1" />
            </>}
          >
            <p></p>
            <p> / TPO ...</p>
          </Card>
        </div>
      </>
    );
  }
}

export default Quality;
