import React, { Component } from 'react';
import { Carousel, Card } from 'antd';
import styles from './index.module.scss';


class Teacher extends Component {
  shouldComponentUpdate() {
    return true;
  }

  render() {
    return (
      <>
        <div className={styles.titleContainer}>
          <p className={styles.title}></p>
          <p className={styles.description}></p>
        </div>
        <Carousel autoplay className={styles.container}>
          <div>
            <div className={styles.teacherBox}>
              <Card
                bordered={false}
                hoverable
                className={styles.antdCard}
              >
                <div>
                  <span className={styles.teacherName}></span>
                  <span className={styles.teacherClass}> - </span>
                </div>
                <div>
                  <p className={styles.teacherClass}> ...</p>
                </div>
              </Card>
              <Card
                bordered={false}
                hoverable
                className={styles.antdCard}
              >
                <div>
                  <span className={styles.teacherName}></span>
                  <span className={styles.teacherClass}></span>
                </div>
                <div>
                  <p className={styles.teacherClass}>6</p>
                </div>
              </Card>
            </div>
          </div>
          <div>
            <div className={styles.teacherBox}>
              <Card
                bordered={false}
                hoverable
                className={styles.antdCard}
              >
                <div>
                  <span className={styles.teacherName}></span>
                  <span className={styles.teacherClass}> - </span>
                </div>
                <div>
                  <p className={styles.teacherClass}>6</p>
                </div>
              </Card>
              <Card
                bordered={false}
                hoverable
                className={styles.antdCard}
              >
                <div>
                  <span className={styles.teacherName}></span>
                  <span className={styles.teacherClass}></span>
                </div>
                <div>
                  <p className={styles.teacherClass}>4,</p>
                </div>
              </Card>
            </div>
          </div>
        </Carousel>
      </>
    );
  }
}

export default Teacher;
